import React, { useEffect } from 'react'
import Header from "./../../Header/Header"
import Merchandise_breadcum from "./Merchandise_breadcum/Merchandise_breadcum"
import Merchandise_first_sec from "./Merchandise_first_sec/Merchandise_first_sec"
import Footer from "./../../FOOTER/Footer"
// import {Tabtitle} from "./../../Tabtitle"
import {SeoContext} from "./../../../Seocontext"

const Merchandise = () => {
  useEffect(()=>{
    window.scrollTo({top:0, behavior:'smooth'})
  },[])
  // Tabtitle("Merchandise - TRG : Raichand")
  return (
    <div>
      <SeoContext>
        <Header/>
        <Merchandise_breadcum/>
        <Merchandise_first_sec/>
        <Footer/>
        </SeoContext>
    </div>
  )
}

export default Merchandise